/* eslint-disable react/no-typos */

/* eslint-disable import/first */

import "./index.scss";
import React from "react";
import { Switch, Route, Link } from "react-router-dom";
import { Layout, Menu, Icon, Dropdown } from "antd";
import { axios, history } from "&";
const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;
import Showscore from "../showscore";
import User from "../user";
import Insertscore from "../insertscore";
import Compare from "../scoreCompare";
import OneObj from "../oneObj";
import moment from "moment";
var timerID;
const menu = (
  <Menu>
    <Menu.Item>
      <Link to="/home/user?key=1">
        <Icon type="user" style={{ marginRight: "8px" }} />
        修改个人信息
      </Link>
    </Menu.Item>
    <Menu.Item>
      <Link to="/login">
        <Icon type="login" style={{ marginRight: "8px" }} />
        登陆
      </Link>
    </Menu.Item>
    <Menu.Item>
      <span
        onClick={() => {
          localStorage.token = "";
          history.push("/login");
        }}
      >
        <Icon type="logout" style={{ marginRight: "8px" }} />
        退出
      </span>
    </Menu.Item>
  </Menu>
);

class SiderDemo extends React.Component {
  constructor() {
    super();
    this.state = {
      collapsed: false,
      username: "",
      shijian: moment().format("YYYY-MM-DD HH:mm:ss")
    };
  }

  onCollapse = collapsed => {
    this.setState({ collapsed });
    console.log(collapsed);
  };

  componentDidMount() {
    if (localStorage.token === "" || !localStorage.token) {
      history.push("/login");
    }
    timerID = setInterval(() => this.tick(), 1000);
    if (localStorage.username === "undefined") {
      this.setState({
        username: "无名氏"
      });
    } else if (!localStorage.username) {
      this.setState({
        username: "无名氏"
      });
    } else {
      this.setState({
        username: localStorage.username
      });
    }
  }
  componentWillUnMount() {
    clearInterval(timerID);
  }
  tick = () => {
    //setState更新组件局部状态
    this.setState({
      shijian: moment().format("YYYY-MM-DD HH:mm:ss")
    });
  };

  render() {
    const { username, shijian } = this.state;
    return (
      <div>
        <Header style={{ color: "#FFF", fontSize: "35px" }}>
          
          <span style={{ position: "relative", right: "0" }}>
            <Icon
              type="user"
              style={{ fontSize: "22px", marginRight: "0.5%" }}
            />
            <Dropdown overlay={menu}>
              <span
                style={{ fontSize: "22px" }}
                className="ant-dropdown-link"
                to="#"
              >
                {username} <Icon type="down" />
              </span>
            </Dropdown>
            <span
            style={{
              fontSize: "16px",
              marginLeft: "2%",
              marginRight: "12px"
            }}
            ref={el => (this.timerID = el)}
          >
            {shijian}
          </span>
          </span>
          <span style={{float:"right"}}> 学生系统管理</span>
         
        </Header>
        <Layout style={{ minHeight: "95vh" }}>
          <Sider
            collapsible
            collapsed={this.state.collapsed}
            onCollapse={this.onCollapse}
          >
            <div className="logo" />
            <Menu theme="dark" defaultSelectedKeys={["sub1"]} mode="inline">
              <SubMenu
                key="sub1"
                title={
                  <span>
                    <Icon type="user" />
                    <span>个人信息</span>
                  </span>
                }
              >
                <Menu.Item key="1">
                  <Link to="/home/user?key=1">修改个人信息</Link>{" "}
                </Menu.Item>
              </SubMenu>
              <Menu.Item key="2">
                <Link to="/home/showscore?key=2">
                  <Icon type="desktop" />
                  <span>班级成绩</span>
                </Link>
              </Menu.Item>
              <Menu.Item key="3">
                <Link to="/home/scoreCompare?key=3">
                  <Icon type="pie-chart" />
                  <span>成绩对比</span>
                </Link>
              </Menu.Item>
              <SubMenu
                key="sub2"
                title={
                  <span>
                    <Icon type="folder" />
                    <span>项目</span>
                  </span>
                }
              >
                <Menu.Item key="4">
                  <Link to="/home/insertscore?key=4">提交项目</Link>
                </Menu.Item>
                <Menu.Item key="5">
                  <Link to="/home/oneObj?key=5">个人项目</Link>
                </Menu.Item>
              </SubMenu>
              <SubMenu
                key="sub3"
                title={
                  <span>
                    <Icon type="flag" />
                    <span>面试</span>
                  </span>
                }
              >
                <Menu.Item key="6">面试宝典</Menu.Item>
                <Menu.Item key="7">面试答案</Menu.Item>
              </SubMenu>
            </Menu>
          </Sider>
          <Layout>
            <Content style={{ margin: "16px 16px" }}>
              <div
                style={{ padding: 24, background: "#fff", minHeight: "80vh" }}
              >
                <Switch>
                  <Route path="/home/showscore" component={Showscore} />
                  <Route path="/home/user" component={User} />
                  <Route path="/home/insertscore" component={Insertscore} />
                  <Route path="/home/scoreCompare" component={Compare} />
                  <Route path="/home/oneObj" component={OneObj} />
                </Switch>
              </div>
            </Content>
            <Footer style={{ textAlign: "center" }}>
              Ant Design ©2018 Created by Ant UED
            </Footer>
          </Layout>
        </Layout>
      </div>
    );
  }
}
export default SiderDemo;
