/* eslint-disable array-callback-return */
/* eslint-disable react/jsx-no-duplicate-props */
/* eslint-disable react/jsx-no-undef */
import { Table, Divider, Tag, Input, Select, Layout,notification,Icon   } from 'antd';
import { axios } from "&"
import React, { Component } from "react";
import score from "../../mobx/score"
import {observer} from "mobx-react"
const { Option } = Select;
const { Search } = Input;
const { Header } = Layout;

var mintor = ''
var type = ''
var data = [];
  
function setdata(res) {
    var data = [];
    res.data.result.forEach((item, index) => {
        var obj = {}
        obj.key = index + 1
        obj.serial = index + 1
        obj.type = item.type
        obj.username = item.username
        obj.projectName = item.projectName
        obj.projectUrl = item.projectUrl
        obj.readme = item.readme
        obj.tech = item.tech
        obj.performance = item.score
        data.push(obj)
    })
    return data
}
@observer

 class Showsocore extends Component {
    constructor() {
        super();
        this.state = {
            mintor: "wh1906",
            type: "vue",
            data: [],
            search:""
        };
    }
    //虚拟DOM挂载真实DOM
    componentDidMount() {
        
        var canshu = {
            mintor,
            type
        } = this.state
        console.log(canshu.mintor)
        axios.post("/react/getall", {
            mintor: canshu.mintor,
            type: canshu.type
        }).then(res => {
            console.log(res.data.result)
             data=setdata(res)
            this.setState({
                data:data
            })

        })
    }
        //定点搜索班级
    handleChange = (val) => {

        val = val.split("-")
        const value = "wh" + val[1]
        this.setState({
            mintor: value
        })
        setTimeout(() => {
            console.log(this.state.mintor)
        
            axios.post("/react/getall", {
                mintor: this.state.mintor,
                type: this.state.type,
                search:this.state.search
            }).then(res => {
          
                console.log(res.data.result)
                data=setdata(res)
                this.setState({
                    data
                })
            })
        }, 100)


    }
    //搜索学科
    handleChangeTow = (value) => {
        this.setState({
            type: value,
        })
        setTimeout(() => {
            console.log(this.state.type)
            axios.post("/react/getall", {
                mintor: this.state.mintor,
                type: this.state.type,
                search:this.state.search
            }).then(res => {
        
                console.log(res.data.result)
                 data=setdata(res)
                this.setState({
                    data
                })
            })
        }, 100)
    }
    //搜索框
    Search=(value)=>{
        this.setState({
            search: value,
        })
        setTimeout(() => {
            console.log(this.state.search)
            axios.post("/react/getall", {
                mintor: this.state.mintor,
                type: this.state.type,
                search:this.state.search
            }).then(res => {
    
                
                console.log(res.data.result)
                 result=setdata(res)
                this.setState({
                    data:result
                })
            
            })
        }, 100)
    }
    render() {
        const {
            mintor
        } = this.state
        const{
            banJi,
            keCheng
        }=score
        return (
            <div>
                <Header style={{ background: '#fff', padding: "0 2%" }}>
                    <span>请选择对应的班级: &nbsp;</span>
                    <Select defaultValue={banJi[banJi.length-1]}  style={{ width: "150px" }} onChange={this.handleChange}>


                        {
                            banJi.map((item, index) => {
                                return (
                                    <Option style={{ padding: "0 2%" }} key={index} value={item}>{item}</Option>
                                )
                            })
                        }

                    </Select>
                    <span style={{ margin: "0 0 0 3%" }}>请选择对应的课程:  &nbsp;</span>
                    <Select defaultValue="vue" style={{ width: "150px" }}  onChange={this.handleChangeTow}>

                        {
                            keCheng.map((item, index) => {
                                return (
                                    <Option style={{ padding: "0 2%" }} key={index} value={item}>{item}</Option>
                                )
                            })
                        }
                    </Select>
                    <Search
                        placeholder="请输入关键字"
                        onSearch={value => this.Search(value)}
                        style={{ width: 200, margin: "0 0 0 3%" }}
                    />
                </Header>
                <h2 style={{ color: "#428bca", fontSize: 30 }}>当前项目详情</h2>
                <Table bordered  columns={columns}  dataSource={data} />
            </div>
        )
    }
}
export default Showsocore;
const columns = [
    //序号
    {
        title: '序号',
        width: "5%",     align:"center",
        dataIndex: 'serial',
        key: 'serial',
    },
    //框架
    {
        title: '框架',
        width: "5%",     align:"center",
        dataIndex: 'type',
        key: 'type',
    },

    {
        title: '开发者',
        width: "5%",     align:"center",
        dataIndex: 'username',
        key: 'username',
    },
    {
        title: '项目名称',
        width: "8%",     align:"center",
        dataIndex: 'projectName',
        key: 'projectName ',
    },
    {
        title: '项目地址',
        width: "12%",     align:"center",
        dataIndex: 'projectUrl',
        key: 'projectUrl',
    },
    {
        title: '备注',
        width: "25%",     align:"center",
        dataIndex: 'readme',
        key: 'readme',
    },
    {
        title: '项目技术亮点',
        width: "25%",     align:"center",

        dataIndex: 'tech',
        key: 'tech',
    },
    {
        title: '成绩',
        width: "5%",     align:"center",
        dataIndex: 'performance',
        key: 'performance',
        sorter: (a, b) => a.performance - b.performance,
    },

];

