import React, { Component } from "react"
import { axios } from "&"
import "./index.scss"
import {
    Input, Select, Button,message
} from 'antd';
import score from "../../mobx/score"
import { observer } from "mobx-react"
const { TextArea } = Input;
const { Option } = Select;
@observer
class Insertscore extends Component {
    constructor() {
        super();
        this.state = {
            isLogin: localStorage.token,
            type: '请选择框架类型',
            tech:"",
            readme:""

        }
    }
    handleChange = (value) => {
        console.log(`selected ${value}`);
        this.setState({
            type: value
        })

    }
    onChange=(e)=>{
        this.setState({
            tech:e.target.value     //改值
        })
    }
       onChange2=(e)=>{
        this.setState({
            readme:e.target.value     //改值
        })
    }
      //重置
      chongZhi = () => {
        this.setState({
            type: '请选择框架类型',
            tech:"",
            readme:""
        })
        this.one.state.value = ''
        this.two.state.value = ""
    }
    //提交
    submit=()=>{
        if (this.one.state.value === '' || this.two.state.value === "" ||  this.state.type === "请选择框架类型" ||  this.state.tech === "" ||  this.state.readme === "") {
            message.info('请完善信息后提交')

        }else{
            if(localStorage.username==="undefined"){
                message.info('请完善个人信息后提交')
            }else{
                console.log(this.one.state.value )
                var date = Date.now();
                if(localStorage.username || localStorage.mintor){
                    axios.post("/react/insertScore", {
                        projectUrl:this.one.state.value,
                        projectName:this.two.state.value,
                        type:this.state.type,
                        readme:this.state.readme,
                        tech:this.state.tech,
                        username:localStorage.username,
                        mintor:localStorage.mintor,
                        _id:localStorage._id,
                        date
                    }).then(res => {
                        this.setState({
                            type: '请选择框架类型',
                            tech:"",
                            readme:""
                        })
                        this.one.state.value = ''
                        this.two.state.value = ""
                       
                    })
                }else{
                    message.info("请将个人信息填写完整")
                }
            }
          
           
        }
        
    }
    
    render() {
        const {
            keCheng
        } = score
        const {
            type,
            tech,
            readme
        } = this.state
        return (
            <div>
                <h2 style={{ color: "#428bca", fontSize: 30 }}>提交项目信息</h2>
                <br />
                <div className="example-input">
                    <p>项目线上地址: </p>
                    <Input placeholder="项目线上地址"  ref={el=>this.one=el}/> <br /><br />
                    <p>项目名称 </p>
                    <Input style={{ width: 300 }}  placeholder="项目名称" ref={el=>this.two=el} /> <br /><br />
                    <p>框架: </p>
                    <Select value={type}   style={{ width: 300 }} onChange={this.handleChange}>

                        {
                            keCheng.map((item, index) => {
                                return (
                                    <Option style={{ padding: "0 2%" }} key={index} value={item}>{item}</Option>
                                )
                            })
                        }
                    </Select>
                    <br /><br />
                    <p>技术亮点: </p>
                    <TextArea style={{ width: 300 }}   onChange={this.onChange} value={tech}  rows={4} ref={el=>this.js=el} placeholder="请至少写出三条技术亮点" />

                    <br /><br />
                    <p>备注: </p>
                    <TextArea style={{ width: 300 }}   onChange={this.onChange2} value={readme}  rows={4} ref={el=>this.beizhu=el} placeholder="请输入注册好的账号和其他备注" />

                    <br /><br />
                    <Button type="primary" onClick={this.submit} style={{ marginLeft: "5%" }}>提交</Button>
                     <Button type="danger" onClick={this.chongZhi}>重置</Button>
                </div>,
            </div>
        )
    }
}

export default Insertscore;