/* eslint-disable react/react-in-jsx-scope */
import { Table, Input, Button, Popconfirm, Form } from "antd";

import { axios, history } from "&";
const EditableContext = React.createContext();

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);
var dataSource = [];
class EditableCell extends React.Component {
  state = {
    editing: false
  };

  toggleEdit = () => {
    const editing = !this.state.editing;
    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
      }
    });
  };

  save = e => {
    const { record, handleSave } = this.props;
    this.form.validateFields((error, values) => {
      if (error && error[e.currentTarget.id]) {
        return;
      }
      this.toggleEdit();
      handleSave({ ...record, ...values });
    });
  };

  renderCell = form => {
    this.form = form;
    const { children, dataIndex, record, title } = this.props;
    const { editing } = this.state;
    return editing ? (
      <Form.Item style={{ margin: 0 }}>
        {form.getFieldDecorator(dataIndex, {
          rules: [
            {
              required: true,
              message: `${title} is required.`
            }
          ],
          initialValue: record[dataIndex]
        })(
          <Input
            ref={node => (this.input = node)}
            onPressEnter={this.save}
            onBlur={this.save}
          />
        )}
      </Form.Item>
    ) : (
      <div
        className="editable-cell-value-wrap"
        style={{ paddingRight: 24 }}
        onClick={this.toggleEdit}
      >
        {children}
      </div>
    );
  };

  render() {
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleSave,
      children,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editable ? (
          <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
        ) : (
          children
        )}
      </td>
    );
  }
}
function setdata(res) {
  var dataSource = [];
  res.data.result.forEach((item, index) => {
    var obj = {};
    obj.key = index + 1;
    obj.serial = index + 1;
    obj.type = item.type;
    obj.username = item.username;
    obj.projectName = item.projectName;
    obj.projectUrl = item.projectUrl;
    obj.readme = item.readme;
    obj.tech = item.tech;
    obj.performance = item.score;
    obj._id = item._id;
    dataSource.push(obj);
  });
  return dataSource;
}
class EditableTable extends React.Component {
  constructor(props) {
    super(props);
    this.columns = [
      {
        title: "序号",
        width: "5%",
        align: "center",
        dataIndex: "serial",
        key: "serial"
      },
      //框架
      {
        title: "框架",
        width: "5%",
        align: "center",
        dataIndex: "type",
        editable: true,
        key: "type"
      },

      {
        title: "开发者",
        width: "5%",
        align: "center",
        dataIndex: "username",
        editable: true,
        key: "username"
      },
      {
        title: "项目名称",
        width: "8%",
        align: "center",
        dataIndex: "projectName",
        editable: true,
        key: "projectName "
      },
      {
        title: "项目地址",
        width: "12%",
        align: "center",
        dataIndex: "projectUrl",
        editable: true,
        key: "projectUrl"
      },
      {
        title: "备注",
        width: "20%",
        align: "center",
        dataIndex: "readme",
        editable: true,
        key: "readme"
      },
      {
        title: "项目技术亮点",
        editable: true,
        align: "center",
        width: "30%",
        dataIndex: "tech",

        key: "tech"
      },
      {
        title: "成绩",
        width: "5%",
        align: "center",
        dataIndex: "performance",

        key: "performance",
        sorter: (a, b) => a.performance - b.performance
      },
      {
        title: "操作1",
        align: "center",
        dataIndex: "updateobj",
        render: (text, record) =>
          this.state.dataSource.length >= 1 ? (
            <Popconfirm
              title="你确定要修改吗?"
              onConfirm={() => this.handleUpdate(record)}
            >
              <a>修改</a>
            </Popconfirm>
          ) : null
      },
      {
        title: "操作2",
        align: "center",
        dataIndex: "operation",
        render: (text, record) =>
          this.state.dataSource.length >= 1 ? (
            <Popconfirm
              title="你确定要删除吗?"
              onConfirm={() => this.handleDelete(record.key, record)}
            >
              <a>删除</a>
            </Popconfirm>
          ) : null
      }
    ];

    this.state = {
      dataSource: [],
      count: 2
    };
  }
  handleUpdate = record => {
    console.log(record);
    let obj={}
     obj._id=record._id;
     obj.projectUrl=record.projectUrl;
     obj.projectName=record.projectName;
     obj.type=record.type;
     obj.readme=record.readme;
     obj.tech=record.tech;
     obj.username=localStorage.username;
     
    axios.post("/react/updateoneObj", obj).then(res => {

    });
  };
  handleDelete = (key, record) => {
    console.log(record._id);

    const dataSource = [...this.state.dataSource];
    this.setState({ dataSource: dataSource.filter(item => item.key !== key) });
    axios.post("/react/deloneObj", { _id: record._id }).then(res => {});
  };

  handleSave = row => {
    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.key === item.key);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row
    });
    this.setState({ dataSource: newData });
  };

  componentDidMount() {
    axios
      .post("/react/getOneObj", {
        uid: localStorage._id
      })
      .then(res => {
        // console.log(res.data.result)
        dataSource = setdata(res);
        this.setState({
          dataSource: dataSource
        });
      });
  }

  render() {
    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };
    const columns = this.columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: record => ({
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          handleSave: this.handleSave
        })
      };
    });
    return (
      <div>
        <Table
          components={components}
          rowClassName={() => "editable-row"}
          bordered
          dataSource={dataSource}
          columns={columns}
        />
      </div>
    );
  }
}
export default EditableTable;
