/* eslint-disable no-dupe-keys */
/* eslint-disable no-useless-constructor */
import React, { Component } from "react"
import "./index.scss"
import {
    Input, Select, Button, message
} from 'antd';
import score from "../../mobx/score"
import { observer } from "mobx-react"
import { history } from "&"
import { connect } from "react-redux";
import { getUserList } from "../../actions"
const { Option } = Select;
@connect(
    state => ({
        user: state.get("user")
    })
)
@observer
class User extends Component {
    constructor() {
        super();
        this.state = {
            isLogin: sessionStorage.token,
            mintor: '请选择班级',
            username:""


        }
    }

    componentDidMount() {
        this.setState({
        })
        if(localStorage.username==="undefined"){
            this.setState({
                username:"请输入您的名字"
            })

        }else{
            this.username.state.value=localStorage.username

        }
        if(localStorage.mintor==="undefined" || !localStorage.mintor){
            this.setState({
                mintor:"请选择班级"
            })

        }else{
            this.state.mintor=localStorage.mintor

        }
        
        // if(sessionStorage.token){
        //     // sessionStorage.phone=this.phone.state.value;
        // }
    }
    handleChange = (val) => {
        sessionStorage.phone = this.phone.state.value;

        val = val.split("-")
        const value = "wh" + val[1]
        console.log(`selected ${value}`);
        this.setState({
            mintor: value
        })

    }
    //重置
    chongZhi = () => {
        this.setState({
            mintor: "请选择班级",
        })
        this.username.state.value = ''
        this.email.state.value = ""
        this.phone.state.value = ""
    }
    //提交
    submit = () => {
        if (this.username.state.value === '' || this.email.state.value === "" || this.phone.state.value === "" || this.state.mintor === "请选择班级") {
            message.info('请完善信息后提交')

        } else {
            localStorage.username = this.username.state.value;
            localStorage.mintor=this.state.mintor;
            var date = Date.now();
            this.props.dispatch(getUserList(
                {
                    url: "/react/setUser",

                    params: {
                        _id:localStorage._id,
                        username: this.username.state.value,
                        mintor: this.state.mintor,
                        email: this.email.state.value,
                        phone: this.phone.state.value,
                        date

                    }
                }
            ))

        }

    }
    render() {
        const {
            banJi
        } = score
        const {
            user
        } = this.props;
        const {
            mintor,username
        } = this.state
        return (
            <div>
                <h2 style={{ color: "#428bca", fontSize: 30 }}>修改个人信息</h2>
                <br />
                <div className="example-input">
                    <span>姓名: </span><br />
                    <Input placeholder={username} allowClear style={{ width: 300 }} ref={el => this.username = el} />
                    <br /><br />
                    <span>班级: </span><br />
                    <Select value={mintor} allowClear style={{ width: 300 }} onChange={this.handleChange}>
                        {
                            banJi.map((item, index) => {
                                return (
                                    <Option style={{ padding: "0 2%" }} key={index} value={item}>{item}</Option>
                                )
                            })
                        }
                    </Select><br /><br />
                    <span>邮箱: </span>
                    <Input placeholder="请输入邮箱" allowClear ref={el => this.email = el} /> <br /><br />
                    <span>手机: </span>
                    <Input placeholder="请输入手机号" value={localStorage.phone} disabled allowClear ref={el => this.phone = el} /> <br /><br />

                    <Button type="primary" style={{ marginLeft: "2%", marginRight: 50 }} onClick={this.submit}>提交</Button>
                    <Button type="danger" onClick={this.chongZhi}>重置</Button>
                </div>,
            </div>
        )
    }

}
export default User;