/* eslint-disable no-useless-constructor */
import React from "react";
import "./index.scss";
import { Input, Select, Button } from "antd";
import echarts from "echarts/lib/echarts";
import { axios, history } from "&";
// 引入柱状图
import "echarts/lib/chart/bar";
import "echarts/lib/chart/line";
import "echarts/lib/chart/pie";
// 引入提示框和标题组件
import "echarts/lib/component/tooltip";
import "echarts/lib/component/title";
import score from "../../mobx/score";
import { observer } from "mobx-react";
const { Search } = Input;
const { Option } = Select;

//柱子图
function action1(a,b){
    var myChart1 = echarts.init(document.getElementById("main1"));
    //         // 绘制图表
    myChart1.setOption({
      title: { text: "班级内成绩对比" },
      tooltip: {},
      xAxis: {
        data:a
      },
      yAxis: {},
      series: [
        {
          name: "成绩",
          type: "bar",
          data: b
        }
      ]
    });
}

//扇形图
function action(a){
    var myChart1 = echarts.init(document.getElementById("main"));

    myChart1.setOption({
      backgroundColor: "#2c343c",
      visualMap: {
        show: false,
        min: 80,
        max: 600,
        inRange: {
          colorLightness: [0, 1]
        }
      },
      series: [
        {
          name: "访问来源",
          type: "pie",
          radius: "55%",
          data: a,
          roseType: "angle",
          label: {
            normal: {
              textStyle: {
                color: "rgba(255, 255, 255, 0.3)"
              }
            }
          },
          labelLine: {
            normal: {
              lineStyle: {
                color: "rgba(255, 255, 255, 0.3)"
              }
            }
          }
        }
      ]
    });
}
//姓名集合
var usernames = [];
function setdatausername(res) {
  var usernames = [];
  res.data.result.forEach((item, index) => {
    usernames.push(item.username);
  });
  return usernames;
}
//成绩集合
var scores = [];
function setdatascores(res) {
  var scores = [];
  res.data.result.forEach((item, index) => {
    scores.push(item.score);
  });
  return scores;
}
//list集合
var list = [];
function setdata(res) {
  var list = [];
  res.data.result.forEach((item, index) => {
    var obj = {};
    obj.value = item.score;
    obj.name = item.username;

    list.push(obj);
  });
  return list;
}

@observer
class Compare extends Component {
  constructor() {
    super();
    this.state = {
      usernames: [],
      scores: [],
      banJi: "wh1903",
      keCheng: "vue"
    };
  }

  componentDidMount() {
    let obj = {};
    obj.mintor = this.state.banJi;
    obj.type = this.state.keCheng;
    console.log(obj);
    axios.post("/react/score", { obj }).then(result => {
      usernames = setdatausername(result);
      scores = setdatascores(result);
      this.setState({
        usernames: usernames,
        scores: scores
      });
      action1( this.state.usernames,this.state.scores)
    });
  }
  handleChange = val => {
    val = val.split("-");
    const value = "wh" + val[1];
    // console.log(value)
    this.setState({
      banJi: value
    });
    setTimeout(() => {
      // console.log(this.state.banJi)

      let obj = {};
      obj.mintor = this.state.banJi;
      obj.type = this.state.keCheng;
      console.log(obj);
      axios.post("/react/score", { obj }).then(result => {
        usernames = setdatausername(result);
        scores = setdatascores(result);
        this.setState({
          usernames: usernames,
          scores: scores
        });
        action1( this.state.usernames,this.state.scores)
      });
    }, 100);
  };
  //搜索学科
  handleChangeTow = value => {
    this.setState({
      keCheng: value
    });
    setTimeout(() => {
      let obj = {};
      obj.mintor = this.state.banJi;
      obj.type = this.state.keCheng;
      console.log(obj);
      axios.post("/react/score", { obj }).then(result => {
        usernames = setdatausername(result);
        scores = setdatascores(result);
        this.setState({
          usernames: usernames,
          scores: scores
        });
        action1( this.state.usernames,this.state.scores)
      });
    }, 100);
  };
  render() {
    const { keCheng, banJi } = score;
    return (
      <span>
        <span style={{ margin: "0 0 0 3%" }}>班级:</span>
        <Select
          defaultValue={banJi[banJi.length - 1]}
          style={{ width: "150px" }}
          onChange={this.handleChange}
        >
          {banJi.map((item, index) => {
            return (
              <Option style={{ padding: "0 2%" }} key={index} value={item}>
                {item}
              </Option>
            );
          })}
        </Select>
        <span style={{ margin: "0 0 0 3%" }}>学科:</span>
        <Select
          defaultValue={keCheng[0]}
          style={{ width: "150px" }}
          onChange={this.handleChangeTow}
        >
          {keCheng.map((item, index) => {
            return (
              <Option style={{ padding: "0 2%" }} key={index} value={item}>
                {item}
              </Option>
            );
          })}
        </Select>

        <div id="main1" style={{ width: "100%", height: 800}}></div>

        <Compare2></Compare2>
      </span>
    );
  }
}

export default Compare;




